body {
  background-color: #282828;
}

.country {
  margin-top: 20px;
}

.newCountryButton {
  position: fixed;
  bottom:0;
  right: 5px;
  text-align: right;
  margin-top: 20px; 
}

.loginLink,
.logout {
  color: #fff;
  position: absolute;
  top: 8px;
  left: 8px;
}

.logoutLink {
  color: #fff;
}

.loginForm { 
  color: #fff;
  border: 1px solid #999;
  background-color: #404040;
  padding: 8px;
  border-radius: 4px;
  position: absolute;
  width: 300px;
  top: 65px;
  left: 8px;
  z-index: 99;
}

